'use client'

import { motion } from 'framer-motion'
import { TrendingUp, TrendingDown } from 'lucide-react'

const tickerData = [
  { symbol: 'NIFTY 50', value: '22,123.45', change: '+0.85%', isUp: true },
  { symbol: 'SENSEX', value: '72,456.78', change: '+0.72%', isUp: true },
  { symbol: 'USD/INR', value: '82.94', change: '-0.12%', isUp: false },
  { symbol: 'GOLD', value: '62,150.00', change: '+1.24%', isUp: true },
  { symbol: 'RELIANCE', value: '2,945.30', change: '+1.45%', isUp: true },
  { symbol: 'HDFC BANK', value: '1,422.15', change: '-0.65%', isUp: false },
  { symbol: 'INFY', value: '1,680.40', change: '+0.32%', isUp: true },
  { symbol: '10Y BOND', value: '7.12%', change: '-0.05%', isUp: false },
]

export default function MarketTicker() {
  // Triple the data to ensure seamless scrolling
  const scrollData = [...tickerData, ...tickerData, ...tickerData]

  return (
    <div className="fixed bottom-0 left-0 right-0 z-[60] h-10 bg-slate-950/40 backdrop-blur-md border-t border-white/5 flex items-center overflow-hidden pointer-events-none select-none shadow-[0_-10px_30px_rgba(0,0,0,0.5)]">
      {/* Neon Top Border Glow */}
      <div className="absolute top-0 left-0 right-0 h-[1px] bg-gradient-to-r from-transparent via-indigo-500/30 to-transparent" />
      
      <motion.div
        animate={{
          x: [0, -1000], // Adjust based on data length for seamless loop
        }}
        transition={{
          duration: 30,
          repeat: Infinity,
          ease: 'linear',
        }}
        className="flex items-center gap-12 whitespace-nowrap px-6"
      >
        {scrollData.map((item, i) => (
          <div key={i} className="flex items-center gap-2">
            <span className="text-[11px] font-bold text-slate-500 uppercase tracking-widest">{item.symbol}</span>
            <span className="text-sm font-medium text-white/90 font-mono">{item.value}</span>
            <span className={`flex items-center gap-0.5 text-xs font-bold ${item.isUp ? 'text-emerald-400 glow-text' : 'text-rose-400 glow-text'}`}>
              {item.isUp ? <TrendingUp size={10} /> : <TrendingDown size={10} />}
              {item.change}
            </span>
          </div>
        ))}
      </motion.div>
    </div>
  )
}
